<template>
  <img
    :src="`${path}/${file.replace(/\s/g, '_').concat('.webp')}`"
    :alt="file"
    :title="file"
    :width="width"
    :height="height"
    loading="lazy"
    @click="onClick()"
  />
</template>

<script>
export default {
  name: "Portrait",
  props: {
    path: {
      value: String,
      default: "",
      required: true
    },
    file: {
      value: String,
      default: "",
      required: true
    },
    width: {
      value: String,
      default: "auto",
      required: false
    },
    height: {
      value: String,
      default: "auto",
      required: false
    }
  },
  methods: {
    onClick() {
      this.$emit("click", this.file);
    }
  }
};
</script>
